.gallery-page {
  min-height: 100vh;
  padding-top: 70px;
}

/* Hero Section */
.gallery-hero {
  padding: 100px 20px 80px;
  text-align: center;
  background: linear-gradient(135deg, #F8F9FA 0%, #E9ECEF 50%, #DEE2E6 100%);
  border-bottom: 1px solid rgba(44, 62, 80, 0.1);
}

.gallery-hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.gallery-title {
  font-family: 'Inter', sans-serif;
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 24px;
  line-height: 1.1;
  color: #2C3E50;
  letter-spacing: -0.02em;
}

.gallery-subtitle {
  font-family: 'Inter', sans-serif;
  font-size: 1.1rem;
  line-height: 1.7;
  color: #5A6C7D;
  max-width: 600px;
  margin: 0 auto;
  font-weight: 400;
}

/* Tab Navigation */
.gallery-tabs {
  padding: 60px 20px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(44, 62, 80, 0.1);
}

.tabs-container {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 28px;
  border: 2px solid transparent;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.9);
  color: #5A6C7D;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(20px);
  box-shadow: 0 4px 12px rgba(44, 62, 80, 0.08);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tab-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(44, 62, 80, 0.15);
  background: rgba(255, 255, 255, 1);
}

.tab-button.active {
  background: var(--category-color);
  color: white;
  border-color: var(--category-color);
  box-shadow: 0 8px 20px rgba(44, 62, 80, 0.2);
}

.tab-icon {
  font-size: 1.2rem;
}

.tab-label {
  font-size: 0.95rem;
}

/* Gallery Grid */
.gallery-grid-section {
  padding: 80px 20px;
  background: rgba(248, 249, 250, 0.5);
  backdrop-filter: blur(20px);
}

.gallery-container {
  max-width: 1200px;
  margin: 0 auto;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 32px;
  padding: 40px 0;
}

.image-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(44, 62, 80, 0.12);
  border: 1px solid rgba(44, 62, 80, 0.08);
  position: relative;
}

.image-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 48px rgba(44, 62, 80, 0.18);
  border-color: rgba(44, 62, 80, 0.15);
}

.image-container {
  position: relative;
  width: 100%;
  height: 280px;
  overflow: hidden;
}

.gallery-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  transition: transform 0.3s ease;
  background: #F8F9FA;
}

.gallery-image:hover {
  transform: scale(1.05);
}

.image-skeleton {
  width: 100%;
  height: 100%;
  border-radius: 0;
}

.placeholder-image {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #E9ECEF, #DEE2E6, #CED4DA);
  background-size: 200% 200%;
  animation: gradientShift 3s ease-in-out infinite;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #5A6C7D;
}

.placeholder-emoji {
  font-size: 2.5rem;
  margin-bottom: 12px;
  display: block;
}

.placeholder-text {
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  text-align: center;
  opacity: 0.8;
  font-weight: 500;
}

/* Special Card Styling */
.special-card {
  border: 3px solid #E74C3C;
  box-shadow: 0 8px 32px rgba(231, 76, 60, 0.2);
}

.special-card:hover {
  box-shadow: 0 16px 48px rgba(231, 76, 60, 0.3);
}

.special-badge {
  position: absolute;
  top: 16px;
  right: 16px;
  background: rgba(231, 76, 60, 0.95);
  color: white;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 0.8rem;
  text-align: center;
  backdrop-filter: blur(20px);
  animation: pulse 2s ease-in-out infinite;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.badge-text {
  display: block;
  font-weight: 600;
  margin-bottom: 4px;
}

.badge-subtitle {
  display: block;
  font-size: 0.7rem;
  opacity: 0.9;
  font-weight: 400;
}

/* Responsive Design */
@media (max-width: 768px) {
  .gallery-title {
    font-size: 2.5rem;
  }
  
  .gallery-subtitle {
    font-size: 1.1rem;
  }
  
  .tabs-container {
    gap: 10px;
  }
  
  .tab-button {
    padding: 12px 20px;
    font-size: 0.9rem;
  }
  
  .gallery-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .image-container {
    height: 200px;
  }
}

@media (max-width: 480px) {
  .gallery-title {
    font-size: 2rem;
  }
  
  .gallery-subtitle {
    font-size: 1rem;
  }
  
  .tabs-container {
    flex-direction: column;
    align-items: center;
  }
  
  .tab-button {
    width: 200px;
    justify-content: center;
  }
  
  .gallery-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .image-container {
    height: 180px;
  }
}
